@use "../base/" as base;

button {
  width: fit-content;
  font-family: base.$fontMono;
  font-weight: 500;
  text-transform: uppercase;
  font-size: 1.3rem;
  padding: 10px 20px;
  cursor: pointer;
  background-color: base.$layer;
  color: base.$secondary;
  border: 1px solid base.$secondary;
  border-radius: 6px;
}
