@use "../base/" as base;

.skill-icon {
  max-width: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  img {
    max-width: 50px;
    height: auto;

    @media (max-width: 400px) {
      max-width: 36px;
    }
  }

  span {
    margin-top: 0.5rem;
  }
}
